@import "../common/styles/sass/variables";

.contactsBlock {
    height: 100vh;
    background-color: $BackgroundColor2;
    flex-wrap: wrap;
    .contactsContainer {
        flex-direction: column;
        justify-content: flex-start;
        .contactsForm{

            width: 100%;
            height: 70vh;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;


        }
        .contactsForm textarea, input {
            width: 40vh;
            margin-bottom: 1rem;

        }
    }

}











